*, *:before, *:after {
  box-sizing: border-box; }

* {
  padding: 0;
  margin: 0; }

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background: #ffffff;
  font: 300 16px "Open Sans", serif;
  line-height: 1.4;
  color: #000000; }

ul, ol {
  padding: 0;
  margin: 0; }

a {
  outline: none;
  color: #000000;
  transition: all .3s;
  text-decoration: none; }
  a:hover, a:focus {
    outline: none; }
  a:hover {
    text-decoration: underline; }

img {
  max-width: 100%;
  height: auto; }

body, html {
  height: 100%;
  font-size: 16px; }

.globwrap {
  min-height: 100%; }

p {
  margin: 0 0 10px 0; }

.bgimg {
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: cover; }

.bgimgcont {
  background-repeat: no-repeat;
  background-position: 50% 50%;
  background-size: contain; }

.dib {
  display: inline-block;
  vertical-align: top; }

.d-table {
  display: table;
  width: 100%; }

.d-cell {
  display: table-cell;
  vertical-align: middle;
  text-align: center; }

.slick-slide {
  outline: none; }
  .slick-slide img {
    display: inline-block; }

.contenttab {
  display: none; }
  .contenttab.activetab {
    display: block; }

.opacity-0 {
  opacity: 0; }

.opacity-1 {
  opacity: 1; }

.wrap {
  max-width: 1200px;
  margin: 0 auto;
  position: relative; }

.hidden {
  display: none; }

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  font-family: "Open Sans", serif;
  font-weight: 500; }

h1 {
  font-size: 30px;
  margin-bottom: 40px;
  text-transform: uppercase; }

h2 {
  font-size: 30px;
  margin-bottom: 40px; }

h3 {
  font-size: 30px; }

h4 {
  margin-bottom: 18px; }

.modal_active {
  overflow: hidden; }

.modal_div {
  width: 800px;
  height: 448px;
  position: fixed;
  top: 45%;
  /* отступаем сверху 45%, остальные 5% подвинет скрипт */
  left: 50%;
  margin-left: -400px;
  margin-top: -224px;
  display: none;
  z-index: 999;
  opacity: 0;
  background: #ffffff; }
  .modal_div .modal_close {
    width: 32px;
    height: 32px;
    position: absolute;
    top: 17px;
    right: -50px;
    cursor: pointer;
    display: block; }

/* Подложка */
.modal_overlay {
  z-index: 99;
  position: fixed;
  background-color: #000;
  opacity: 0.8;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  cursor: pointer;
  display: none; }

.wrapp {
  position: absolute;
  max-width: 1920px;
  width: 100%;
  margin: 0 auto; }

.section {
  position: relative; }

.container-wr {
  max-width: 1170px;
  padding-right: 15px;
  padding-left: 15px;
  margin: 0 auto; }

.flex-row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px; }
